"use client"

import { useState } from "react"
import { IncidentDetails } from "@/types/incidentDetails"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"

interface IncidentDetailsTableProps {
    data: IncidentDetails[]
}

export function IncidentDetailsTable({ data }: IncidentDetailsTableProps) {
    const [rows, setRows] = useState<IncidentDetails[]>(data)
    const [editIndex, setEditIndex] = useState<number | null>(null)
    const [isAdding, setIsAdding] = useState(false)
    const [newRow, setNewRow] = useState<IncidentDetails>({
        type: "",
        deviation: "",
        description: "",
        sku: "",
        details: "",
        outOfTotal: "",
        poNo: "",
        lotNo: "",
        containerNo: "",
        billOfLandingNo: "",
    })

    const handleSave = () => {
        if (isAdding) {
            setRows([...rows, newRow])
            setIsAdding(false)
            setNewRow({
                type: "",
                deviation: "",
                description: "",
                sku: "",
                details: "",
                outOfTotal: "",
                poNo: "",
                lotNo: "",
                containerNo: "",
                billOfLandingNo: "",
            })
        } else if (editIndex !== null) {
            const updated = [...rows]
            updated[editIndex] = newRow
            setRows(updated)
            setEditIndex(null)
        }
    }

    const handleEdit = (idx: number) => {
        setEditIndex(idx)
        setNewRow(rows[idx])
    }

    return (
        <div className="space-y-4">
            <div className="flex justify-end m-2">
                {isAdding || editIndex !== null ? (
                    <Button onClick={handleSave}>Save</Button>
                ) : (
                    <Button onClick={() => setIsAdding(true)}>+ Add </Button>
                )}
            </div>
            <div className="overflow-x-auto">
                <table className="min-w-full border text-sm text-left">
                    <thead className="bg-gray-100">
                        <tr>
                            <th className="px-4 py-2 border">Type</th>
                            <th className="px-4 py-2 border">Deviation</th>
                            <th className="px-4 py-2 border">Description</th>
                            <th className="px-4 py-2 border">SKU</th>
                            <th className="px-4 py-2 border">Details</th>
                            <th className="px-4 py-2 border"># Out of Total Product</th>
                            <th className="px-4 py-2 border">PO No.</th>
                            <th className="px-4 py-2 border">Lot No.</th>
                            <th className="px-4 py-2 border">Container No.</th>
                            <th className="px-4 py-2 border">Bill of Landing No.</th>
                            <th className="px-4 py-2 border">Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        {rows.map((incident, idx) => (
                            <tr key={idx} className="odd:bg-white even:bg-gray-50">
                                {editIndex === idx ? (
                                    <>
                                        {Object.keys(newRow).map((field) => (
                                            <td key={field} className="px-2 py-2 border">
                                                <Input
                                                    value={(newRow as any)[field]}
                                                    onChange={(e) =>
                                                        setNewRow({ ...newRow, [field]: e.target.value })
                                                    }
                                                />
                                            </td>
                                        ))}
                                        <td className="px-4 py-2 border">Editing</td>
                                    </>
                                ) : (
                                    <>
                                        <td className="px-4 py-2 border">{incident.type}</td>
                                        <td className="px-4 py-2 border">{incident.deviation}</td>
                                        <td className="px-4 py-2 border">{incident.description}</td>
                                        <td className="px-4 py-2 border">{incident.sku}</td>
                                        <td className="px-4 py-2 border">{incident.details}</td>
                                        <td className="px-4 py-2 border">{incident.outOfTotal}</td>
                                        <td className="px-4 py-2 border">{incident.poNo}</td>
                                        <td className="px-4 py-2 border">{incident.lotNo}</td>
                                        <td className="px-4 py-2 border">{incident.containerNo}</td>
                                        <td className="px-4 py-2 border">{incident.billOfLandingNo}</td>
                                        <td className="px-4 py-2 border">
                                            <Button
                                                size="sm"
                                                variant="outline"
                                                onClick={() => handleEdit(idx)}
                                            >
                                                Edit
                                            </Button>
                                        </td>
                                    </>
                                )}
                            </tr>
                        ))}

                        {isAdding && (
                            <tr className="bg-gray-50">
                                {Object.keys(newRow).map((field) => (
                                    <td key={field} className="px-2 py-2 border">
                                        <Input
                                            value={(newRow as any)[field]}
                                            onChange={(e) =>
                                                setNewRow({ ...newRow, [field]: e.target.value })
                                            }
                                        />
                                    </td>
                                ))}
                                <td className="px-4 py-2 border">Adding</td>
                            </tr>
                        )}
                    </tbody>
                </table>
            </div>


        </div>
    )
}
